const mutations = {
  chapter: (state, stat) => (state.e1 = stat),
  getBarangay: (state, BarangayList) => (state.Barangay = BarangayList),
  assignPopulation: (state, res) => (state.population = res),
  assignWasteGen: (state, res) => (state.wasteGen = res),

  mutateHeader: (state, res) => (state.headerID = res),
  mutateCity: (state, res) => (state.cityID = res),
}

export default {
  state,
  getters,
  actions,
  mutations
};