import axios from 'axios'
import con from './config'


const state = {

       links: [],
       sublinks: [],
       basepath: window.location.origin,
       basepath1: 'http://localhost/',
       sessionState: [],
}

const getters = {
       allLinks: state => state.links,
       allSubLinks: state => state.sublinks,
       globalBasepath: state => state.basepath,
       getStateSession: state => state.sessionState,
}
const actions = {

       async getIISToken({ dispatch, commit }, t) {
              // localStorage.setItem('origToken', t.token)
              // localStorage.setItem('origTokenID', t.id)
              // localStorage.setItem('originNum', 0)
              localStorage.setItem('originNum', 'iis')
              return await axios.get(`https://iis.emb.gov.ph/embis/pbsapi/?token=${t.token}&token_id=${t.id}`, {
              }).then(function (res) {
                     return { employee_id: res.data['employee_id'], origin: 0 }

              }).catch(error => {

              })

       },

       // http://localhost:8080/?token_id=4&token=2524&origin=crs#/
       // http://swm.emb.gov.ph/swm/?token_id=4&token=2524&origin=crs#/
       async getCRSToken({ }, t) {
              // localStorage.setItem('origToken', t.user_id)
              // localStorage.setItem('origTokenID', t.company_id)
              // localStorage.setItem('originNum', 1)
              localStorage.setItem('originNum', 'crs')
              return await axios.get(`https://iis.emb.gov.ph/embis/api/Crs_Api/json_company_swm?api_key=x&company_id=${t.company_id}&user_id=${t.user_id}`, {

              }).then(function (res) {
                     console.log(res)
                     // localStorage.setItem('employee_id', res.data['userinfo']['client_id'])
                     localStorage.setItem('region_name', res.data['region_name'])
                     localStorage.setItem('region_id', res.data['rgnid'])
                     localStorage.setItem('province_id', res.data['province_id'])
                     localStorage.setItem('city_id', res.data['city_id'])
                     return { employee_id: res.data['userinfo']['client_id'], origin: 1 }
              }).catch(error => {

              })



       },

       async loginAPI({ commit }, t) {
              return await con.post(`/auth/login`, {
                     iis_employee_number: t.tokens,
                     origin: t.origin
              }).then(function (log) {
                     console.log(t.origin)
                     if (log.data.user != null) {

                            if (t.origin != 1) {
                                   // if (log.data.user['region'] == "19") {
                                   //        sessionStorage.setItem('user_type', 'nswmc');
                                   // } else 
                                   if (log.data.user['region'] == "18") {
                                          sessionStorage.setItem('user_type', 'central');
                                          localStorage.setItem('user_type', 'central')
                                   } else {
                                          sessionStorage.setItem('user_type', 'region');
                                          localStorage.setItem('user_type', 'region')
                                   }
                            } else {
                                   sessionStorage.setItem('user_type', 'lgu');
                                   localStorage.setItem('user_type', 'lgu')
                            }
                            sessionStorage.setItem('ascwsktken', log.data.access_token);
                            localStorage.setItem('ascwsktken', log.data.access_token)
                            sessionStorage.setItem('login', 1);
                            window.location.href = "/";

                     } else {
                            alert('User is not register or deactivated, please contact administrator')
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                            window.location.href = "https://client.emb.gov.ph/crs/";

                            if (t.origin == 0) {
                                   window.location.href = "https://iis.emb.gov.ph/embis/";
                            } else {
                                   window.location.href = "https://client.emb.gov.ph/crs/";
                            }
                     }

                     return log.data.access_token


              }).catch(error => {
                     console.log(error.response.data.error)
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            localStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })


       },

       async getBackendData({ commit }, t) {
              await con.post(`/GetBackendData`, {
                     tokens: t.token,
                     origin: t.origin
              }).then(function (res) {
                     var new_data = res['data']['data']
                     if (new_data != null) {
                            sessionStorage.setItem('login', 1);
                            commit('setSessionMutation', new_data);
                     } else {
                            alert('User is not register or deactivated, please contact administrator')
                            if (t.origin == 0) {
                                   window.location.href = "https://iis.emb.gov.ph/embis/";
                            } else {
                                   window.location.href = "https://client.emb.gov.ph/crs/";
                            }
                     }

              }).catch(error => {
                     console.log(error);
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            localStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })
       },

       //Sample of API function to override Config header
       async getSidebar2({ commit }, res) {

              await axios.get(`http://localhost/swms_api/public/GetResLink/${res.access_level}`, {
                     headers: {
                            'Authorization': `token ${res.token}`,
                            'Content-Type': 'application/json',
                            'X-Requested-With': 'XMLHttpRequest'

                     }
              }).then(function (res) {
                     commit('allLinks', res.data['Res']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            localStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })
       },

       async getSidebar({ commit }, res) {
              await con.get(`/GetResLink/${res.access_level}`).then(function (res) {
                     commit('allLinks', res.data['Res']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            localStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })
       },

       async getSubSidebar({ commit }, res) {
              await con.get(`/GetSubResLink/${res.access_level}`).then(function (res) {
                     commit('allSubLinks', res.data['Res']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            localStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })


       },

};

const mutations = {
       allLinks: (state, User) => (state.links = User),
       allSubLinks: (state, User) => (state.sublinks = User),
       setSessionMutation: (state, data) => (state.sessionState = data),
}

export default {
       state,
       getters,
       actions,
       mutations
};