const mutations = {
  setPlanList: (state, PlanList) => (state.PlanList = PlanList),
  currentRow: (state, Irow) => (state.currentRowNo = Irow),
  // getRegions: (state, RegionList) => (state.Regions = RegionList),
  // getProvince: (state, ProvinceList) => (state.Provinces = ProvinceList),
  // getCities: (state, CityList) => (state.Cities = CityList),
  setChapterData: (state, c) => (state.Chapter = c),
  getAttach: (state, c) => (state.Attachments = c),
  // getregionId: (state, regionIdres) => (state.regionId = regionIdres),
  // getwacsProf: (state, wacsProfres) => (state.WacsProf = wacsProfres),
  // getStatus: (state, statusres) => (state.Status = statusres),
  setHistory: (state, result) => (state.History = result),
  getTypAttach: (state, result) => (state.TypAttachments = result),
  allPersonnel: (state, User) => (state.personnel = User),
  allGroup: (state, data) => (state.group = data),
}

export default {
  state,
  getters,
  actions,
  mutations
};